.teaser {
  position: relative;
  overflow: hidden;
  background-color: $accent-dark;
  background-size: cover;
  background-image: url(../img/teaser_background-desktop.png);
  background-repeat: no-repeat;
  min-height: 878px;
}

.teaser__wrapper {
  position: relative;
  display: flex;
  max-width: 1490px;
  margin: 0 auto;
  margin-top: 170px;
}

.teaser__button {
  position: relative;
  display: block;
  width: 55.1%;
  margin-left: 13.3%;
  padding: 2.4% 3%;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  border: 1px solid $blue;

  &:hover,
  &:focus {
    color: darken(#ffffff, 15%);
    border: 1px solid $blue;
    background-color: lighten($blue, 10%);
  }

  & span:last-child {
    display: none;
  }
}

.teaser__scroll-button {
  width: 101px;
  padding: 0;
  background-color: transparent;
  border: none;
  position: absolute;
  font-family: "ptmono", "Arial", sans-serif;
  font-weight: normal;
  text-transform: none;
  font-size: 14px;
  line-height: 16px;
  transform: rotate(-90deg);
  left: -5px;
  top: 553px;
  opacity: 0.5;
  letter-spacing: normal;

  &:hover,
  &:focus {
    border: none;
    background-color: transparent;
    color: white;
    opacity: 1;
  }
}



.teaser__text-wrapper {
  width: 54%;
  padding-left: 2.3%;
  padding-top: 143px;

  & h1 {
    margin: 0;
    padding: 0;
    font-family: "roboto-condensed", "Arial", sans-serif;
    font-weight: 700;
    font-size: 80px;
    letter-spacing: 0.07em;
    line-height: 94px;
    text-transform: uppercase;
    margin-bottom: 30px;
  }

  & p {
    margin: 0;
    position: relative;
    padding-left: 13.3%;
    font-size: 22px;
    line-height: 34px;
    margin-bottom: 5.8%;

    &::before {
      position: absolute;
      content: "";
      width: 9%;
      height: 2px;
      background-color: #fff;
      top: 16px;
      left: 0.4%;
    }
  }
}

.teaser__image-wrapper {
  position: relative;
  top: -53px;
  left: 43px;
  width: 783px;

  & img {
    width: 713px;
    position: relative;
    background-repeat: no-repeat;
  }
}


@media (max-width: $desktop-width) {
  .teaser__scroll-button {
    display: none;
  }

  .teaser {
    min-height: 686px;
    background-image: url(../img/teaser-background-tablet.png);
    background-size: cover;
  }

  .teaser__wrapper {
    position: relative;
    padding-left: 11.5%;
    margin-top: 187px;
  }

  .teaser__text-wrapper {
    width: 100%;
    padding: 0;

    & h1 {
      width: 300px;
      font-size: 60px;
      line-height: 70px;
      letter-spacing: 0.07em;
      margin-bottom: 32px;
    }

    & p {
      font-size: 18px;
      line-height: 156.8%;
      padding-left: 50px;
      padding-right: 252px;
      margin-bottom: 54px;

      &::before {
        width: 4.8%;
      }
    }
  }

  .teaser__button {
    margin-left: 51px;
    width: 379px;
  }

  .teaser__image-wrapper {
    position: absolute;
    width: 260px;
    left: auto;
    right: -2px;
    top: -74px;

    &::before {
      position: absolute;
      content: "";
      width: 528.64px;
      height: 505.5px;
      top: -152px;
      left: -69px;
      background-image: url(../img/teaser-decoration.svg);
      background-size: cover;
      z-index: 0;
    }
  }

}

@media (max-width: $tablet-width) {

  .teaser {
    min-height: 647px;
    background-image: url(../img/teaser-background-mobile.png);
  }

  .teaser__wrapper {
    padding-left: 4.7%;
    margin-top: 167px;
  }

  .teaser__text-wrapper {
    padding-top: 118px;
    position: relative;
    z-index: 3;

    & h1 {
      font-size: 40px;
      line-height: 47px;
      letter-spacing: 0.07em;
      margin-bottom: 29px;
    }

    & p {
      font-size: 16px;
      width: 245px;
      margin-bottom: 42px;

      &::before {
        width: 6.1%;
        top: 14px;
      }
    }
  }

  .teaser__image-wrapper {
    width: 297px;
    top: -76px;
    right: -2px;

    &:before {
      top: -138px;
      left: 126px;
      width: 505.02px;
      height: 482.92px;
    }
  }

  .teaser__button {
    margin-left: 3px;
    width: 94%;
    min-height: 55px;

    & span:first-child {
      display: none;
    }

    & span:last-child {
      display: inline;
    }
  }


}
