.logo {
  width: 202px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  & span {
    display: block;
  }

  & svg {
    width: 52px;
    height: 52px;
  }
}

.logo__text-wrapper {
  & p {
    margin: 0;
    padding: 0;
    font-family: "roboto-condensed", "Arial", sans-serif;
    font-weight: 700;
    font-size: 25px;
    line-height: 29px;
  }

  & span {
    font-family: "roboto", "Arial", sans-serif;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    text-transform: lowercase;
    color: #788891;
  }
}


.logo--page-footer {
  width: 100%;
  display: block;
  max-width: 217px;
  flex-wrap: wrap;
  padding-bottom: 47px;


  & svg {
    margin-right: 6px;
  }

  & p {
    color: #203C42;
  }

  & .logo__text-wrapper {
    width: 148px;
    display: inline-block;
    position: relative;
    top: -6px;
  }
}

@media (max-width: $desktop-width) {
  .logo--page-footer {
    padding: 0;
  }
}

@media (max-width: $tablet-width) {

  .logo {
    width: auto;
    & svg {
      width: 38px;
      height: 38px;
    }
  }

  .logo__text-wrapper {
    display: none;
  }

  .logo--page-footer {
    display: flex;
    justify-content: center;
    max-width: 100%;

    & .logo__text-wrapper {
      position: static;
    }

    & svg {
      margin-right: 13px;
      width: 52px;
      height: 52px;
    }
  }
}
